*,:after,:before{margin:0;padding:0;box-sizing:border-box;-webkit-font-smoothing:antialiased;font-smoothing:antialiased;text-rendering:optimizeLegibility;outline:none}html{font-family:Roboto}body{background-color:#fefefe}.Layout{width:100%;display:flex;justify-content:center;background-color:#84ceeb}.Layout__container{width:1260px;max-width:100%;box-shadow:0 2px 4px 0 rgba(0,0,0,.2);background-color:#fff}.ToolBar{display:flex;width:100%;height:100px;justify-content:space-between;padding:10px 20px;align-items:center;border-bottom:1px solid rgba(0,0,0,.2)}.NavigationItems{list-style:none;display:inline-flex}.NavigationItem{padding:0 5px}.NavigationItem a{color:#000;text-decoration:none;text-transform:uppercase;font-size:16px}.NavigationItem a:hover{border-bottom:5px solid #5ab9ea;padding-bottom:5px;font-weight:700}.PageTitleCard{width:100%;height:100px;display:flex;align-items:center;background-color:#eee;border-bottom:1px solid rgba(0,0,0,.2)}.PageTitleCard__heading{font-size:22px;color:#666;padding-left:10px}.PageTitleCard__subheading{font-size:16px;color:#666;padding-left:10px}.DiscoverContainer{width:100%;display:flex;flex-wrap:wrap}.DiscoverList{width:100%;padding:20px 0 50px;text-align:center}.DiscoverList h2{padding-bottom:20px}.DiscoverListItem{box-shadow:0 4px 8px 0 rgba(0,0,0,.2);padding:10px;width:20%;margin:20px;background-color:#fff;transition:.3s;text-align:center}.DiscoverListItem:hover{cursor:pointer;box-shadow:0 8px 16px 0 rgba(0,0,0,.2)}.LocationList{width:100%;height:100%;display:flex;align-items:center;background-color:#5680e9}.LocationList__container{width:100%}.LocationList__title{text-align:center;font-size:22px;color:#fff;padding:10px}.LocationList__list{list-style:none;display:flex;width:100%;flex-wrap:wrap;justify-content:center}.LocationItem{width:20%;display:flex;flex-wrap:wrap;padding:10px;margin:20px;text-align:center;background-color:#fff;transition:.3s;box-shadow:0 4px 8px 0 rgba(0,0,0,.2)}.LocationItem:hover{cursor:pointer;box-shadow:0 8px 16px 0 rgba(0,0,0,.2)}.LocationItem span{width:100%}.DiveSiteList{width:100%;display:flex;flex-wrap:wrap;padding:20px}.DiveSiteList>span{text-align:center;width:100%;font-size:24px;border-bottom:3px solid #5680e9;padding-bottom:6px}.DiveSiteList>ul{padding:20px 0;list-style:none}.Discover{border-bottom:1px solid rgba(0,0,0,.2)}.Discover,.Discover ul{width:100%;display:flex;flex-wrap:wrap}.Discover ul{list-style:none;justify-content:center}